"use client";
import React, { useState, useMemo } from "react";
import Image from "next/image";
import { Box, Typography, Pagination } from "@mui/material";
import LocationOnOutlinedIcon from "@mui/icons-material/LocationOnOutlined";

export default function Main_image() {
  // Expanded image collection
  const images = [
    "/Images/imageviewmain.png",
    "/Images/view_imagechanger2.png",
    "/Images/Popular3.svg",
    "/Images/imageviewmain.png",
    ...Array.from({length: 30}, (_, i) => `/Images/image${i+1}.jpg`)
  ];

  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const [page, setPage] = useState(1);
  const imagesPerPage = 4;

  // Paginated images
  const paginatedImages = useMemo(() => {
    const startIndex = (page - 1) * imagesPerPage;
    return images.slice(startIndex, startIndex + imagesPerPage);
  }, [page, images]);

  const totalPages = Math.ceil(images.length / imagesPerPage);

  const handlePageChange = (event, value) => {
    setPage(value);
    setCurrentImageIndex((value - 1) * imagesPerPage);
  };

  const handleImageClick = (index) => {
    setCurrentImageIndex(index);
  };

  return (
    <Box sx={{ position: "relative", bottom: { md: "40px", xs: "50px", sm: "50px" } }}>
      {/* Main Image Display */}
      <Box sx={{ width: { xs: "100%", sm: "100%", lg: "1530px" }, display: "flex", justifyContent: "center" }}>
        <Box sx={{ width: "100%", maxWidth: "1000px" }}>
          <Image
            src={images[currentImageIndex]}
            alt={`Selected Image`}
            width={1000}
            height={600}
            style={{ objectFit: "cover", borderRadius: "10px" }}
          />
        </Box>
      </Box>

      {/* Pagination Section */}
      <Box sx={{ 
        display: 'flex', 
        justifyContent: 'center', 
        alignItems: 'center', 
        gap: 2, 
        mt: 2 
      }}>
        <Pagination 
          count={totalPages} 
          page={page} 
          onChange={handlePageChange} 
          color="primary" 
          size="large" 
        />
      </Box>

      {/* Thumbnail Images */}
      <Box sx={{ 
        display: 'flex', 
        justifyContent: 'center', 
        gap: 2, 
        mt: 2, 
        overflowX: 'auto' 
      }}>
        {paginatedImages.map((image, index) => (
          <Box 
            key={index} 
            onClick={() => handleImageClick((page - 1) * imagesPerPage + index)}
            sx={{ 
              width: 100, 
              height: 100, 
              cursor: 'pointer',
              border: currentImageIndex === (page - 1) * imagesPerPage + index 
                ? '3px solid blue' 
                : '1px solid gray',
              borderRadius: 2
            }}
          >
            <Image 
              src={image} 
              alt={`Thumbnail ${index + 1}`} 
              width={100} 
              height={100} 
              style={{ objectFit: 'cover', borderRadius: 8 }}
            />
          </Box>
        ))}
      </Box>
    </Box>
  );
}